@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@400;500;600;700&display=swap");
.header-container {
  position: relative;
  left: 0;
  padding: 5px;
  width: 100vw;
  height: 71px;
  display: flex;
  z-index: 10;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-flow: row wrap;
  background-color: #fff;
  border-bottom: pink;
}
@media (max-width: 370px) {
  .header-container {
    height: fit-content;
  }
}

.header-legend-container {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  position: absolute;
  top: 1rem;
  left: 1rem;
  height: 15px;
  cursor: pointer;
}
.header-legend-container .header-legend-text-container {
  display: flex;
}
.header-legend-container .header-legend-text-container p {
  margin: 0;
  margin: auto;
  font-size: 0.85rem;
  text-align: center;
  font-family: "Outfit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  animation: colorCycle 60s infinite;
}
.header-legend-container .header-legend-text-container .header-legend-arrow {
  font-size: 1rem;
  transform: rotate(-90deg);
  margin-left: 0px;
  transition: all 0.5s ease; /* Add a smooth transition effect */
}
.header-legend-container.active .header-legend-arrow {
  transform: rotate(0);
  margin-left: 5px;
  transition: all 0.5s ease; /* Add a smooth transition effect */
}

.header-title {
  display: block;
  margin: 0px;
  font-weight: 800;
  font-size: 30px;
  font-family: "Outfit", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  animation: colorCycle 60s infinite;
}

@keyframes colorCycle {
  0% {
    color: #7fd5e0;
  }
  9.0909090909% {
    color: #f5ce6d;
  }
  18.1818181818% {
    color: #ad7ee8;
  }
  27.2727272727% {
    color: #db2323;
  }
  36.3636363636% {
    color: #9ab9f2;
  }
  45.4545454545% {
    color: rgb(4, 32, 27);
  }
  54.5454545455% {
    color: #e8a3ff;
  }
  63.6363636364% {
    color: #aeb4b2;
  }
  72.7272727273% {
    color: #a25f25;
  }
  81.8181818182% {
    color: #0f1e7e;
  }
  90.9090909091% {
    color: rgb(237, 236, 232);
  }
}